@import "../../scss/variables";
@import "../../scss/mixins";

.desktop {
    position:absolute;
    padding-top: 4rem;
    padding-right: 2.5rem;
    width: 100vw;
    height: 100vh;

    @media screen and (max-width: 500px) {
        padding-right: 1.7rem;
    }

    &__icons {
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        @media screen and (max-height: 860px) {
            flex-direction: row;
            & > *:not(:last-child){
                margin-right: 3rem;
            }
        }
    }
}
